<template lang="html">
  <div class="peony-image-light">
    <image-gallery
      :value="value"
      :action="action"
      :optional="optional"
      :deletable="deletable"
      :handle-url-method="handleUrlMethod"
      :image-width="imageWidth"
      :image-height="imageHeight"
      :show-badge="showBadge"
      :openFeedback="openEvent"
      :selectFeedback="selectEvent"
      :removeFeedback="removeEvent"
    ></image-gallery>
    <image-slider
      ref="image-slider"
      v-model="index"
      :list="value"
      image-mode="both"
      :action="action"
      :thumbsAction="thumbsAction"
      :show-more="showMore"
      :handle-url-method="handleUrlMethod"
      :handle-thumbs-url-method="handleThumbsUrlMethod"
    >
      <template slot-scope="props">
        <slot v-bind="props"></slot>
      </template>
    </image-slider>
  </div>
</template>

<script>
import ImageGallery from '../image-gallery/image-gallery.vue' // 
import ImageSlider from '../image-slider/image-slider.vue' // 

export default {
  name: 'PeonyImageLight',
  components: { ImageGallery, ImageSlider },
  props: {
    // 
    value: { type: Array, required: true }, // 
    action: { type: String, default: '' }, // 
    thumbsAction: { type: String, default: '' }, // 
    optional: {type: Boolean, default: false}, // 
    deletable: {type: Boolean, default: false}, // 
    handleUrlMethod: Function, // Function (action, url) {} undefined
    handleThumbsUrlMethod: Function, // Function (action, url) {} undefined
    showMore: {type: Boolean, default: true}, // 
    // 
    imageWidth: {type: [String, Number], default: 280}, // : 200
    imageHeight: {type: [String, Number], default: 172}, // : 164
    showBadge: {type: Boolean, default: true}, // 
    // 
    selectFeedback: Function, //  Function (index, item, event) {}
    removeFeedback: Function //  Function (index, item) {}
  },
  data () {
    return {
      index: 0 // 
    }
  },
  methods: {
    openEvent (index, item) {
      console.log('openEvent', index, item)
      this.index = index
      this.$refs['image-slider'].openSlider()
    },
    removeEvent (index, item) { // 
      if (this.$props.removeFeedback !== undefined) {
        this.$props.removeFeedback(index, item)
      }
      // this.$emit('removeEvent', index, item)
    },
    selectEvent (index, item, event) { // 
      if (this.$props.selectFeedback !== undefined) {
        this.$props.selectFeedback(index, item, event)
      }
      // this.$emit('selectEvent', index, item, event)
    }
  }
}
</script>



